import { defineComponent } from 'vue';
import AvatarMenu from './AvatarMenu.vue';
import { useConfig } from '@/config';
export default defineComponent({
    name: 'Menu',
    components: {
        AvatarMenu
    },
    setup() {
        const { menu } = useConfig();
        return { menu };
    },
    computed: {
        currentHash() {
            return this.$route.hash?.substr(1);
        }
    }
});
