import { defineComponent } from 'vue';
export default defineComponent({
    name: 'Popover',
    data() {
        return {
            isOpen: false
        };
    },
    methods: {
        toggle() {
            if (this.isOpen) {
                this.close();
            }
            else {
                this.open();
            }
        },
        open() {
            this.isOpen = true;
        },
        close() {
            this.isOpen = false;
        }
    }
});
