function getViewByKey(key) {
    return config.views.find(view => view.key === key);
}
function getViewHeadings(key) {
    let heading = {};
    config.menu.forEach(section => {
        const item = section.items.find(item => item.name === key);
        if (item) {
            heading = {
                section: section.label,
                title: item.label
            };
        }
    });
    return heading;
}
export function useConfig() {
    return {
        menu: config.menu,
        dropdownMenu: config.dropdownMenuActions,
        views: config.views,
        defaultViewKey: config.defaultView,
        getViewByKey,
        getViewHeadings
    };
}
